.tasks{
    width:90%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2vh;
    flex-direction: column;
   
}

.addtask{
    width:70%;
    
    display: flex;
    justify-content: center;
    
}

.addtask input{
    width:calc(100% - 75px);
    padding:5px;
    margin-right: 10px;
    text-align: center;
    border: 2px solid #311b92;
    color: #311b92;
    font-size: 16px;
}



.addbutton{
    background-color: #fff59d;
    width: 75px;
    height: 40px;
    font-size: 16px;
    font-weight: bold;
    color: #311b92;
    transition: 0.5s all ease;

}

.addbutton:hover{
    background-color:  #311b92;
    color: #fff59d;
    cursor: pointer;


}

.tasklist{
    height:90%;
    overflow: scroll;
   
    width: 70%;
    display:flex;
    align-items: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 10px;
    margin-top: 40px;
    padding-left: 30px;
    padding-right: 30px;

    border: 1px solid #ffeb3b;
}

