.taskcard{
    display: flex;
    width: 100%;
    padding: 10px ;
    flex-direction: column;
   
    
}

.taskaction{
    display: flex;
    align-items: center;
    

}

button{
    width:75px;
    height:30px;
    border: 1px solid #311b92;
    color: #311b92;
    background-color: white;
    margin-left: 15px;
}

button:hover{
    color:white;
    background-color: #311b92;
    cursor: pointer;
}

input[type="checkbox"]{
    width: 25px;
    height:25px;
    
}

input:checked{
    background-color: #311b92;
    color:white;
}

.nonedit{
    outline: None;
    border: none;
    margin-left: 10px;
    font-size: 18px;
    width:80%;
    height:30px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    background-color: #ede7f6 ;
}

.editable{
    outline: None;
    border: 1px solid #0e0439c8;
    margin-left: 10px;
    font-size: 18px;
    width:80%;
    height:30px;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.time{
    font-size: 14px;
    margin-top:5px;
    color:#453f62;
    margin-left: 35px;
    border: none;
}

.save{
    background-color: #311b92;
    color: white;
}

.save:hover{
    background-color: #0e0439;
}

